.page-404 {
  width: 100%;
  height: 100vh;
  display: flex;
  background-color: $light-yellow;
}

.page-404__inner {
  position: relative;
  max-width: 1440px;
  width: 100%;
  padding: 48px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.page-404__info {
  text-align: center;
}

.page-404__img {

  & img {
    display: block;
    height: calc(100vh - 180px);
    object-fit: contain;
  }
}

.page-404__text {
  margin: 0;
  font-size: 14px;
  line-height: 18px;
}

.page-404__link {
  position: absolute;
  top: 49%;
  font-size: 24px;
  color: $black;

  @media (max-width: $tablet-width) {
    top: 120px;
    font-size: 20px;
  }

  &:hover {
    text-decoration: underline;
  }

  &--catalog {
    left: 25px;
  }

  &--contacts {
    right: 25px;
  }
}
